@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,400,700);

img.portrait {
  width: 60px;
  height: 60px;
}

.link {
  margin: 0 10px;
}

.header {
  width: 100%;
  margin-bottom: 10px;
}

.tabs {
  padding: 10px;
  height: calc(100vh - 148px);
  width: 100%;
}

.content {
  overflow: scroll;
  height: 100%;
  width: 100%;
}

.footer {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  height: 64px;
}

.localizationSelector {
  float: right;
}

table, th, td {
  border: 1px solid black;
}

.flex {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin-bottom: 10px;
}

.select-label {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 0 0 auto;
          flex: 0 0 auto;
  margin: 5px;
}

.select {
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
  display: -webkit-flex;
  display: flex;
}

table, th, td {
  border: 1px solid black;
}

body {
  color: #303034;
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

